body {
  margin: 0;
  color: white;
  overflow-x: hidden;
  --custom-coral: #EE786E;
  --text-dark: rgb(74, 78, 81);;
}

.page {
  display: flex;
  width: 100%;
  height: 100vh;
}

.left, .right {
  width: 50%;
  height: 100%;
}

.center {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.intro.page > .text-overlay {
  position: absolute;
  z-index: 1;
  top: 5vw;
  bottom: 5vw;
  left: 7vw;
  right: 7vw;
  font-family: 'Montserrat', Arial, sans-serif;
}

.name-bar {
  height: 30px;
  width: fit-content;
  display: flex;
  align-items: center;
}

.name-bar > div:first-child > * {
  margin: -3px 0;
  font-size: 16px;
  font-weight: 800;
}

.divider {
  margin-left: 16px;
  height: 100%;
  width: .15vw;
  min-width: 2px;
  background: currentColor;
}

.social-media {
  font-size: 24px;
  margin-left: 16px;
  cursor: pointer;
  transition: all .2s ease-in-out;
}

.social-media:first-child {
  margin-left: 0;
}

.description-bar {
  position: absolute;
  bottom: 0;
}

.description-bar > svg {
  cursor: pointer;
  font-size: 50px;
  transition: all .2s ease-in-out;
}

.social-media:hover,
.description-bar > svg:hover {
  color: var(--custom-coral);
  transform: scale(1.2);
}

.about.page {
  height: fit-content;
  min-height: 100vh;
  font-family: 'Open Sans', sans-serif;
}

.about.page > .left {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 40%;
  background-color: rgba(238, 238, 238, 0.6);
}

.about.page .center.top {
  display: flex;
  flex-direction: column;
  position: relative;
  background-color: #2d3e50;
  height: 32vh;
  width: 100%;
}

.icon {
  cursor: pointer;
  background: 
    url(/my-photo.png) no-repeat 10px 10px / cover,
    linear-gradient(113.83124106282105deg, rgba(248, 198, 215,1) 4.927083333333334%,rgba(175, 233, 249,1) 97.84374999999999%);
  width: 80px;
  height: 80px;
  border-radius: 50%;
  transition: all 0.3s ease-in-out;
}

.icon:hover {
  transform: scale(1.25, 1.25);
}

.about.page .center.top h3 {
  font-size: 28px;
  font-weight: 600;
  margin: 10px 0;
}

.about.page .center.top h4 {
  margin: 0;
}

.about.page .center.top::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  background: #2d3e50;
  top: 28.5vh;
  height: 7vh;
  transform: skewY(-5deg);
}

.about.page .bottom {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: var(--text-dark);
  flex: 1;
  padding-top: 8vh;
  padding-bottom: 8px;
}

.about.page .bottom > * {
  width: 80%;
  margin-right: auto;
  margin-left: auto;
  color: #9099a0;
}

.about.page .bottom > div:first-child {
  display: flex;
  flex-direction: row;
}

.about.page .bottom > div:first-child > * {
  display: flex;
  flex-direction: column;
}

.about.page .bottom > div:first-child > div:first-child > * {
  width: 140px;
  color: var(--text-dark);
  font-weight: bold;
  margin-top: 20px;
}

.about.page .bottom > div:first-child > div:last-child > * {
  flex: 1;
  margin-top: 20px;
  font-weight: normal;
}

.about.page > .right {
  color: #555;
  max-width: 50vw;
  margin: auto auto;
  padding: 20px 0;
}

.about.page .right h2 {
  color: #2d3e50;
  display: inline-flex;
  align-items: center;
}

.about.page .right .dot {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 42px;
  height: 42px;
  border-radius: 50%;
  font-size: 20px;
  color: white;
  background-color: #2d3e50;
  margin-right: 10px;
}

.about.page .right h2 svg::before {
  display: inline-block;
  content: '';
  width: 20px;
  height: 20px;
  border-radius: 50%;
  font-size: 12px;
  color: white;
  background-color: #2d3e50;
  margin-right: 8px;
}

.experience {
  display: flex;
  flex-direction: column;
  padding-left: 43px;
  position: relative;
  margin-bottom: 20px;
}

.experience > div:first-child {
  display: flex;
}

.experience .position {
  color: #2d3e50;
  font-weight: bold;
  white-space:'no-wrap';
}

.experience .company {
  margin-left: 6px;
}

.experience .time {
  margin-top: 10px;
  margin-bottom: 24px;
  opacity: 0.7;
}

.experience .description > p {
  padding-left: 15px;
  margin: 0 0 10px 0;
}

.experience .description::before {
  position: absolute;
  content: '-';
  display: inline-block;
  font-size: 16px;
  margin-right: 10px;
}


.experience::before {
  content: '';
  display: inline-block;
  position: absolute;
  width: 16px;
  height: 16px;
  margin-left: -34px;
  margin-top: -4px;
  margin-right: 10px;
  border-radius: 50%;
  border: #2d3e50 4px solid;
  transition: 300ms;
}

.experience::after {
  content: '';
  display: inline-block;
  position: absolute;
  top:0;
  bottom:0;
  margin-top: 16px;
  margin-left: -24px;
  width: 3px;
  height: calc(100% - 16px);
  background: #3d405b;
}

.experience:hover::before {
  background-color: #2d3e50;
}

.skill-category {
  cursor: pointer;
  position: relative;
  font-size: 12px;
  margin: 0 14px 0 20px;
  padding: 4px;
  overflow: hidden;
}

.skills-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.skill-container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 300ms ease-in-out;
}

.progress-circle {
  cursor: pointer;
  width: 12vw;
  transform: rotate(-90deg);
  transition: all 600ms ease-in-out;
  
  fill: none;
  stroke: #2d3e50;
  stroke-width: 8;
  stroke-linecap: round;
  stroke-dasharray: 0 999;
}

.skill-container > span {
  cursor: pointer;
  position: absolute;
  max-width: 8vw;
  word-wrap: break-word;
}


.interests-wrapper > *:hover, .interests-wrapper > *:hover svg,
.skill-container:hover {
  color: var(--custom-coral);
  transform: scale(1.2);
}

.skill-container:hover .progress-circle {
  transition-duration: 300ms;
  stroke: var(--custom-coral);
  transform: rotate(-90deg) scale(0.88);
}

.interests-wrapper {
  display: flex;
  justify-content: space-between;
  padding: 0 5%;
}

.interests-wrapper > * {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 300ms ease-in-out;
  cursor: pointer;
}

.interests-wrapper > * svg {
  color: #2d3e50;
  font-size: 50px;
  transition: all 300ms ease-in-out;
  margin-bottom: 6px;
}

.transition-circle {
  clip-path: circle(0%);
  height: 100vh;
  width: 100vw;
  position: fixed;
  z-index: 1;
  background: var(--custom-coral);
  animation: circle-in 2500ms;
}

.transition-circle.out {
  animation-direction: reverse;
}

@keyframes circle-in {
  0% { clip-path: circle(100%); }
  50% { clip-path: circle(25%); }
  100% { clip-path: circle(0%); }
}

@media only screen and (max-width: 1160px) {
  .about.page {
    flex-direction: column;
  }
  .about.page > .left {
    position: relative;
    width: 100vw;
    height: 100vh;
  }
  .about.page .right {
    width: unset;
    max-width: 86vw;
  }
  .about.page .center.top::after {
    display: none;
  }
  .skills-wrapper {
    flex-direction: column;
  }
  .progress-circle {
    width: 240px;
    height: 240px;
  }
  .skill-container > span {
    max-width: 220px;
  }
  .about.page .bottom > div:first-child > div:first-child > * {
    width: 25vw;
  }
  .interests-wrapper {
    flex-wrap: wrap;
  }
  .interests-wrapper > * {
    margin-top: 10px;
    min-width: 70px;
  }
}